<p class="sub-title">my work 01;</p><h2><div class="strong">My Work Value. 2019.</div> Developed for CLAP for Youth@JC using <a href="https://unity.com/" target="_blank">Unity</a></h2><div class="flex images"><div class="flex-full"><img class="small" src="@/assets/images/workvalue-sm.jpg" data-aos="zoom-out-up" data-aos-anchor-placement="center-center"></div><div class="screenshot flex-zero"><img src="@/assets/images/workvalue.jpg" data-aos="fade-up-left"></div></div><div class="description"><p class="sub-title">description;</p><p class="content">Built for mobile and web platform. A causal game where players need to drag their fingers or cursors on the screen to apply forces to the character. Mainly using <span class="strong">Unity's 2D Physics Engine</span> and <span class="strong">Sprite System</span> to build the game.</p></div>